const initialState = {
  question: {},
  loading: true,
  error: null,
};

const singleQuestionReducer = (state = initialState, action) => {
  switch (action.type) {
    case "QUESTION_REQUESTED":
      return {
        ...state,
        question: {},
        loading: true,
        error: null
      };
    case "QUESTION_LOADED":
      return {
        ...state,
        question: action.payload,
        loading: false,
        error: null
      };
    case "QUESTION_ERROR":
      return {
        ...state,
        question: {},
        loading:false,
        error: action.payload
      };
    default:
      return state;
  }
};

export default singleQuestionReducer;